<script>
	import { Gateway, FetchComponent } from '@douganderson444/svelte-component-gateway';
	import Proxcryptor from './Proxcryptor.svelte';
	import IPFSManager from './IPFSManager.svelte';

	import { Peerpiper } from '@peerpiper/awesome-peerpiper-components';
	import TagManager from '../../../awesome-peerpiper-components/compiled/@peerpiper/TagManager.svelte.js?raw';
	import { onMount } from 'svelte';

	import HypnsManager from './HypnsManager.svelte';
	import PiperNetManager from './PiperNetManager.svelte';

	export let wallet = null; // possible the user has their own wallet?

	//props
	let Web3WalletMenu;
	let rootCID;
	let openHypns;
	let handleLoadContacts;

	// Option A, get compoennts from url
	let url =
		'https://raw.githubusercontent.com/PeerPiper/awesome-peerpiper-components/master/compiled/@peerpiper/ContactCard.svelte.js';

	// Option B, get component from library
	// let component = '../../';

	let start = Date.now();

	onMount(async () => {
		if (!wallet) {
			// @ts-ignore
			({ Web3WalletMenu } = await import('@peerpiper/svelte-web3-wallet-connector'));
		}
	});

	function refresh() {
		globalThis.ipfsNode.stop();
		globalThis.ipfsNode = null;
		globalThis.location.reload();
		return 'Reloading page...';
	}
</script>

<svelte:head>
	<link href="https://unpkg.com/tailwindcss@^2/dist/tailwind.min.css" rel="stylesheet" />
</svelte:head>
<!-- First, you need a wallet -->
{#if Web3WalletMenu}
	<svelte:component this={Web3WalletMenu} bind:wallet />
{/if}

<!-- HypnsManager for YOUR details...  -->
<!-- When there is data saved to ILPD, why not save it to PipeNet?  -->

<div class="m-2 p-2 border bg-slate-400 rounded text-slate-300">
	<HypnsManager {wallet} {rootCID} bind:openHypns />
</div>

<!-- PiperNetManager for your CONTACTS details -->
<!-- listens for updates in contacts, then listens for updates on PiperNet  -->
<PiperNetManager let:handleLoadContacts {openHypns}>
	<!-- IPFS Manager will only load the slot content once it's a had a chance to load the rootCID, if any  -->
	<!-- IPFS Manager passes both ipfs and rootCID back to this component once intiialized -->
	<!-- IPFS Manager will also pass the preferred tag to load up (current defaults to first entry in rootCID object) -->
	<IPFSManager let:ipfsNode bind:rootCID let:tagNodes>
		<!-- <DataDisplay {ipfsNode} {rootCID} /> -->
		<!-- Fetch gets the component from the interwebs and passes it back up here through let:component  -->
		<!-- TODO: Get the component from the decrypted rootCID object  -->
		<!-- <FetchComponent {url} let:component> -->
		{!ipfsNode ? refresh() : ''}
		{!wallet ? 'Connect to PeerPiper Wallet' : ''}

		<!-- // TODO: Refresh the component when the rootCID changes  -->
		<div class="flex flex-1">
			{#if ipfsNode && wallet}
				<!-- on:change bubbles the emited 'change' event to the parent component, if bind:props isn't avail -->
				<!-- <Proxcryptor
					proxcryptor={wallet?.proxcryptor}
					tag={'Contacts'}
					{ipfsNode}
					{rootCID}
					let:props
					let:handleChange
					on:rootCID={(e) => (rootCID = e.detail)}>
				-->

				<!-- TODO: Get the component from the decrypted rootCID object  -->
				<!-- <PeerContacts {props} on:change={handleChange} /> -->
				<!-- </Proxcryptor> -->
				{#if TagManager}
					<Proxcryptor
						proxcryptor={wallet?.proxcryptor}
						{ipfsNode}
						{rootCID}
						let:decryptTagNode
						let:handleTagChange
						on:rootCID={(e) => (rootCID = e.detail)}
						on:loadContacts={handleLoadContacts}
					>
						<!-- Publishing our latest rootCID to a public Key -->
						<!-- Getting the latest rootCID for Contacts... -->
						<Peerpiper.TagConfig {tagNodes} {decryptTagNode} let:params>
							<!-- TODO: Get the component from the decrypted rootCID object  -->
							<Gateway esModule={TagManager} props={{ params }} on:change={handleTagChange} />
						</Peerpiper.TagConfig>
					</Proxcryptor>
				{/if}
			{/if}
		</div>
		<!-- </FetchComponent> -->
	</IPFSManager>
</PiperNetManager>
